@import 'partials/fonts';
@import '../libs/fontawesome-free-5.10.1-web/scss/fontawesome';
@import 'partials/variables';
@import 'partials/mixins';
@import '../libs/slick/slick/slick.scss';
// @import '../../../build/assets/i/svg/bg/css/sprite.css'
html,
body {
  font-size: 17px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-size: 20px;
  line-height: 1.4;
  color: $text-color;
  background: white;
  @media screen and (max-width: 630px) {
    font-size: 16px;
  }
}

.accent {
  color: $accent;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  @include reset-list;
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

@media screen and (max-width: $xl) {
  .container-big {
    width: 768px;
  }
}

@media screen and (max-width: $md) {
  .container-big {
    padding: 0 30px;
    width: 320px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1215px;
}

.btn {
  position: relative;
  display: table;
  margin: 0 auto;
  padding: 10px 20px;
  text-decoration: none;
  color: white;
  background: $accent;
  transition: all .4s;
  z-index: 4;

  &:hover {
    background: $gulf-stream;
  }

  &.arrow {
    padding-left: 30px;
    padding-right: 46px;
  }

  &.large {
    padding-left: 48px;
    padding-right: 48px;
  }
}

.address {
  &__item {
    display: flex;
    align-items: center;
  }

  picture {
    margin-top: -2px;
    margin-right: 6px;
    height: 18px;
  }

  a {
    font-size: 15px;
    text-decoration: none;
    color: inherit;
  }
}

.sandwitch {
  position: absolute;
  top: 40px;
  right: 30px;
  display: none;
  width: 23px;
  height: 14px;
  cursor: pointer;
  z-index: 100000000;
  @media screen and (max-width: 550px) {
    top: 100px;
  }
  @media screen and (max-width: 1100px) {
    display: block;
  }


}

.slider-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 60px;
  z-index: 1000;
  cursor: pointer;
  opacity: .4;
  transition: all .4s ease;

  &:hover {
    opacity: 1;
  }

  &--left {
    left: -80px;
  }

  &--right {
    right: -80px;
  }
}

.slick-dots {
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;

  li {
    margin-right: 4px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: transparent;
    border: 2px solid #ccc;
    transition: all .4s ease;

    &.slick-active {
      background: #ccc;
      border-color: transparent;
    }

    button {
      font-size: 0;
      background: none;
      border: none;
      outline: none;
    }
  }
}

.sandwitch__line--bottom {
  top: 12px;
}

.sandwitch__line--top {
  top: 0;
}

.sandwitch--active .sandwitch__line--top {
  top: 6px;
  transform: rotate(45deg);
}

.sandwitch__line--middle {
  top: 6px;
}

.sandwitch--active .sandwitch__line--middle {
  opacity: 0;
}

.sandwitch--active .sandwitch__line--bottom {
  top: 6px;
  transform: rotate(-45deg);
}

.sandwitch__line {
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  transition: all linear 0.1s;
  background-color: black;
}


.main-header {
  &-top {
    padding: 10px 0;
    font-size: 15px;
    color: white;
    background: $shark;
    @media screen and (max-width: 630px) {
      display: none;
    }

    .fas {
      margin-right: 10px;
      color: $accent;
    }

    &__wrap {
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin: 0 auto;
      padding: 0 15px;
      max-width: 1200px;
      @media screen and (max-width: 630px) {
        display: block;
      }

      & > * {
        @media screen and (max-width: 630px) {
          margin: 15px 0;
        }

        &:first-child {
          @media screen and (max-width: 630px) {
            margin-top: 0;
          }
        }

        &:last-child {
          @media screen and (max-width: 630px) {
            margin-bottom: 0;
          }
        }
      }

    }

    a {
      font-size: inherit;
      text-decoration: none;
      color: inherit;
    }

    picture {
      margin-top: -2px;
      margin-right: 6px;
      height: 18px;
    }

    &__item {
      display: flex;
      align-items: center;
    }
  }

  //top

  &-bottom {
    position: relative;
    background: white;
    z-index: 2;

    &__wrap {
      display: flex;
      @media screen and (max-width: 850px) {
        display: block;
        padding-bottom: 60px;
      }

    }

    .logo {
      position: static;
      display: block;
    }

    .flags-styled {
      position: relative;
      top: 20px;
      @media screen and (max-width: 1100px) {
        position: absolute;
        top: 30px;
        right: 80px;
        display: flex;
        cursor: pointer;
        z-index: 10;
      }
      @media screen and (max-width: 550px) {
        top: 96px;
      }


      & > * {
        &:not(:last-child) {
          margin-right: 6px;
        }
      }

      .flag.wpglobus-current-language {
        border-bottom: 1px solid $accent;
      }
    }
  }
}

.section {
  padding: 40px 0 60px;

  &.dark {
    position: relative;
    color: white;
    z-index: 1;

    .section__title::before,
    .section__title::after {
      background: white;
    }

    .section__subtitle {
      position: relative;
      color: white;
      z-index: 3;
    }
  }

  &__title {
    position: relative;
    display: table;
    margin: 0 auto 50px;
    @include raleway;
    font-size: 35px;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    @media screen and (max-width: 640px) {
      font-size: 20px;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 60px;
      height: 3px;
      background: $martinique;
      @media screen and (max-width: 930px) {
        display: none;
      }

    }

    &::before {
      left: -100px;
    }

    &::after {
      right: -100px;
    }
  }

  &__subtitle {
    margin-bottom: 30px;
    text-align: center;
    color: $accent;
    @media screen and (max-width: 420px) {
      display: none;
    }
  }

  &__text {
    display: table;
    margin: 0 auto 30px;
    max-width: 530px;
    height: 80px;
    font-size: 17px;
    text-align: center;
    @media screen and (max-width: 420px) {
      display: none;
    }
  }
}

.main-menu {
  display: flex;
  align-items: center;
  @media screen and (min-width: 1100px) {
    display: flex !important;
  }
  @media screen and (max-width: 1100px) {
    position: relative;
    display: none;
    margin-left: 0;
    z-index: 1;
  }
  @media screen and (max-width: 850px) {
    margin-top: 24px;
  }

  &.active {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    padding-top: 40px;
    margin: 0;
    width: 100%;
    height: 100%;
    background: white;
    z-index: 1000;

    li {
      text-align: center;
    }

    a {
      text-transform: uppercase;
    }
  }

  li {
    padding: 20px 27px;

    &.current-menu-item {
      a {
        color: $accent;
      }
    }
  }

  a {
    display: block;
    font-size: 14px;
    text-decoration: none;
    text-transform: uppercase;
    color: $text-color;
    transition: all .4s ease;

    &:hover {
      color: $accent;
    }
  }
}

.main-slider {

  &__wrap {
    position: relative;
    margin: 0 auto;
    max-width: 1350px;
  }

  &__item {
    position: relative;
    height: 600px;
    @media screen and (max-width: 630px) {
      height: 225px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, .6);
    }
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 33px 0;
    width: 820px;
    color: white;
    text-align: center;
    transform: translate(-50%, -50%);
    z-index: 1;
    @media screen and (max-width: 860px) {
      max-width: 820px;
      width: 100%;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../i/main-slider/fs.webp');
      background-repeat: no-repeat;
      @media screen and (max-width: 860px) {
        display: none;
      }

    }

    h2 {
      margin-bottom: 20px;
      font-size: 40px;
      text-transform: uppercase;
      @media screen and (max-width: 630px) {
        font-size: 20px;
      }

    }

    h4 {
      font-size: 20px;
      color: $accent;
      @media screen and (max-width: 630px) {
        font-size: 16px;
      }

    }

    p {
      font-size: 18px;
      font-weight: bold;
      @media screen and (max-width: 630px) {
        font-size: 16px;
      }

    }

    a {
      text-transform: uppercase;
      font-weight: bold;
      color: white;
    }
  }
}

.main-slider-arrows {
  .slider-arrow {
    @media screen and (max-width: 1140px) {
      display: none;
    }

    z-index: 10000;

    svg {
      width: 74px;
      fill: white;
    }
  }

  .slider-arrow--left {
    left: 20px;
  }

  .slider-arrow--right {
    right: 20px;
  }
}

.offers__content {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 30px;
  @media screen and (max-width: 960px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 420px) {
    display: block;
  }
}

.offers__item {
  text-align: center;
  @media screen and (max-width: 420px) {
    margin-bottom: 30px;
  }

  img {
    display: table;
    margin: 0 auto 40px;
    width: 100px;
  }
}

.offers__text {
  h3 {
    text-transform: uppercase;
    font-weight: normal;
    color: $accent;
  }

  p {
    font-size: 14px;
  }
}

//members
.member {
  position: relative;
  background-size: cover;
  background-position: center;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .4);
  }
}

.member__content {
  position: relative;
  display: grid;
  margin-bottom: 70px;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  z-index: 3;
  @media screen and (max-width: 1200px) {
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto 70px;
    max-width: 620px;
  }
  @media screen and (max-width: 600px) {
    display: block;
  }


}

.member__item {
  width: 290px;
  height: 294px;
  background: url('../i/members/members__item_bg.jpg');
  @media screen and (max-width: 620px) {
    margin: 0 auto 30px;
  }


  img {
    position: relative;
    top: 36px;
    display: table;
    margin: 0 auto 20px;
  }
}

.member__text {
  display: table;
  margin: 0 auto;
  padding: 20px;
  padding-bottom: 12px;
  width: 290px;
  font-weight: bold;
  text-align: center;
  color: $martinique;
}

//members
//directions
.directions__content {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 5px;
  margin-bottom: 70px;
  @media screen and (max-width: 940px) {
    grid-template-columns: repeat(3, 1fr);
    //grid-row-gap: ;
  }
  @media screen and (max-width: 600px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 370px) {
    display: block;
  }


}

.directions__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 260px;
  color: white;
  background-size: cover;
  z-index: 1;
  @media screen and (max-width: 370px) {
    margin-bottom: 10px;
  }

  &:hover::after {
    background: rgba(0, 0, 0, .9);
    cursor: pointer;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .4);
    z-index: 2;
    transition: all .4s ease;
  }
}

.directions__center {
  position: relative;
  @include robotolight;
  font-size: 26px;
  text-align: center;
  z-index: 4;

  h4 {
    margin-bottom: 20px;
  }
}

//directions
//idea
.idea {
  position: relative;
  margin: 0 auto;
  max-width: 1350px;
}

.idea-slider {
  position: relative;
  z-index: 1;
}

.idea-slider__item {
  position: relative;
  min-height: 255px;
  background-size: cover;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .4);
    z-index: 2;
  }
}

.idea__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  @include raleway;
  text-align: center;
  color: white;
  z-index: 3;

  h2 {
    position: relative;
    padding-bottom: 15px;
    margin: 0 auto 15px;
    font-size: 34px;
    text-transform: uppercase;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 0;
      width: 640px;
      height: 4px;
      background: white;
      @media screen and (max-width: 630px) {
        width: 85px;
      }

    }

    @media screen and (max-width: 630px) {
      width: auto;
      font-size: 20px;
    }

  }

  p {
    margin: 0 auto 30px;
    max-width: 500px;
    font-size: 18px;
    @media screen and (max-width: 630px) {
      font-size: 14px;
    }

  }
}

//idea
//partners
.partners {
  padding: 40px 0 80px;
  background: $alabaster;

  h2 {
    margin-bottom: 40px;
    @include raleway;
    font-size: 28px;
    text-align: center;
    text-transform: uppercase;
    @media screen and (max-width: 630px) {
      font-size: 20px;
    }

  }
}

.partners-slider-wrap {
  position: relative;
  margin: 0 auto;
  max-width: 950px;

  .slider-arrow--left {
    @media screen and (max-width: 1100px) {
      left: 10px;
    }
  }

  .slider-arrow--right {
    @media screen and (max-width: 1100px) {
      right: 10px;
    }
  }

  .slider-arrow {
    @media screen and (max-width: 900px) {
      display: none;
    }

  }
}

.partners-slider__item {
  img {
    display: table;
    margin: auto;
    height: 80px;
  }
}

//partners
//footer
.footer {
  position: relative;
  padding: 30px;
  background-size: cover;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .8);
    z-index: 2;
  }

  .container {
    position: relative;
    max-width: 992px;
    color: white;
    z-index: 3;
  }

  h2 {
    position: relative;
    padding-bottom: 15px;
    margin-bottom: 20px;
    @include robotolight;
    font-size: 20px;
    text-transform: uppercase;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 40px;
      height: 4px;
      background: white;
    }
  }
}

.footer-top {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  margin-bottom: 30px;
  @media screen and (max-width: 890px) {
    display: block;
  }
}

.footer-contacts {
  padding-left: 40px;
  @media screen and (max-width: 890px) {
    margin-bottom: 30px;
  }

}

.footer-list {
  li {
    display: flex;
    align-items: center;
    margin-bottom: 14px;
  }

  .fas {
    margin-right: 10px;
    color: $accent;
  }

  img {
    width: 18px;
  }
}

.footer-form {
  @media screen and (max-width: 890px) {
    max-width: 600px;
  }

  .form-flex {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    margin-bottom: 20px;
    @media screen and (max-width: 460px) {
      display: block;
    }

    .form-half:first-child {
      @media screen and (max-width: 460px) {
        margin-bottom: 20px;
      }

    }

    input {
      display: block;
      width: 100%;
      padding: 0 10px;
      height: 30px;
      line-height: 30;
      background: none;
      color: white;
      border: 1px solid white;
      @include placeholder-input(white);
    }
  }

  textarea {
    display: block;
    margin-bottom: 20px;
    width: 100%;
    padding: 10px;
    background: none;
    color: white;
    border: 1px solid white;

  }

  input[type="submit"] {
    display: table;
    margin-left: auto;
    margin-right: 30px;
    width: auto;
    color: white;
    background: $abbey;
    border: none;
  }
}

.footer-bottom {
  text-align: center;
}

//footer
//page_intro
.page-intro {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 224px;
  color: white;
  background-size: cover;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .6);
    z-index: 1;
  }

  .section__title {
    position: relative;
    margin-bottom: 0;
    font-weight: bold;
    z-index: 3;

    &::before,
    &::after {
      background: white;
    }
  }
}

//page_intro
//advantage__description
.advantage__description {
  padding: 34px 0;
  margin: 0 auto;
  max-width: 604px;
  text-align: center;
  background: white;
}

.advantage__item {
  padding: 20px 0 40px;

  &.dark {
    background: $alabaster;
  }

  &.reverse {
    .advantage__block {
      grid-template-columns: 1fr 400px;
    }

    .advantage__img {
      order: 2;
    }

    .advantage__text {
      order: 1;
    }
  }

  .section__title {
    &::before,
    &::after {
      background: $accent;
    }
  }
}

.advantage__block {
  display: grid;
  grid-template-columns: 400px 1fr;
  grid-gap: 30px;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 960px;
  @media screen and (max-width: 770px) {
    display: block;
  }
}

.advantage__img {
  @media screen and (max-width: 770px) {
    margin-bottom: 30px;
    text-align: center;
  }

}

.advantage__text {
  @media screen and (max-width: 770px) {
    margin: 0 auto;
    max-width: 600px;
  }

  ul {
    list-style-type: none;
  }

  li {
    position: relative;
    margin-bottom: 14px;
    padding-left: 20px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 12px;
      width: 8px;
      height: 8px;
      background: $accent;
      border-radius: 50%;
    }
  }
}

//advantage__description
//benefice
.benefice {
  padding: 70px 0;
  background: $alabaster;
}

.benefice__content {
  margin: 0 auto;
  max-width: 960px;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 740px) {
    display: block;
    text-align: center;
  }

  p {
    font-weight: bold;
    @media screen and (max-width: 740px) {
      margin-bottom: 30px;
    }

  }

  .btn {
    flex: 0 0 280px;
    text-align: center;
  }
}

//benefice
//our-members
.our-members-content {
  margin: 0 auto;
  padding: 40px 15px;
  max-width: 960px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  font-size: 16px;
  @media screen and (max-width: 850px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 600px) {
    display: block;
  }

  a {
    color: inherit;
    transition: all .4s;

    &:hover {
      text-decoration: underline;
      color: $accent;
    }

    &[href^="mailto"],
    &[href^="tel"] {
      text-decoration: none;
    }
  }
}

.our-members__item {
  @media screen and (max-width: 600px) {
    margin: 0 auto 30px;
    max-width: 400px;
  }

}

.our-members__img {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  height: 100px;

  picture {
    margin-top: auto;
  }
}

//our-members
//news
.news-content {
  padding: 26px 15px;
  margin: 0 auto;
  max-width: 970px;
}

.news__img {
  background-size: cover;
  @media screen and (max-width: 920px) {
    margin-bottom: 30px;
    height: 400px;
  }
  @media screen and (max-width: 500px) {
    height: 300px;
  }


}

.news__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  color: $text-light-color;
  margin-bottom: 30px;
  @media screen and (max-width: 920px) {
    display: block;
  }

  &.single {
    display: block;
    margin: 0 auto;
    max-width: 970px;
    padding: 30px 15px;
    .news__block {
      padding-left: 100px;
      padding-right: 100px;
    }
    .news__item-text {
      p {
        margin-bottom: 24px;
        height: auto;
        overflow: auto;
      }
      img {
        margin-bottom: 24px;
        width: 100%;
      }
    }
  }

}

.news__block {
  padding: 40px 50px;
  border: 1px solid #ccc;
}

.news__item-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.news__item-title {
  display: inline-block;
  vertical-align: middle;
  margin-right: 6px;
  font-size: 12px;
  font-weight: normal;
}

.news__small-logo {
  margin-right: 10px;
  width: 29px;
  height: 29px;
}

.news__crown {
  position: relative;
  top: 2px;
  display: inline-block;
  width: 15px;
}

.news__item-time {
  font-size: 12px;
}

.news__item-date {
  position: relative;
  padding-right: 8px;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 2px;
    width: 2px;
    height: 2px;
    background: #000;
    border-radius: 50%;
  }
}

.news__item-text {
  margin-bottom: 30px;

  h2 {
    margin-bottom: 20px;
    font-size: 26px;
    font-weight: normal;
    height: 92px;
    line-height: 1.1;
    color: $text-color;
    overflow: hidden;

    a {
      text-decoration: none;
      color: inherit;

      &:hover {
        color: blue;
      }
    }
  }

  p {
    height: 50px;
    font-size: 14px;
    overflow: hidden;
    @media screen and (max-width: 480px) {
      height: 60px;
    }

  }
}

.news__item-footer {
  display: flex;
  align-items: center;
  padding-top: 30px;
  font-size: 12px;
  border-top: 1px solid #ccc;

  a {
    font-size: inherit;
    text-decoration: none;
    color: inherit;
  }
}

.news__show {
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
}

.news__like {
  display: flex;
  align-items: center;
  margin-left: auto;
  fill: transparent;
  stroke: red;
  transition: all .3s;

  &-number {
    display: inline-block;
    vertical-align: middle;
    margin-right: 8px;
  }

  svg {
    cursor: pointer;

    &.active {
      fill: red;
    }
  }
}

.socials {
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
  border-top: 1px solid #ccc;
}
.socials__links {
  a {
    display: inline-block;
    margin-right: 30px;
    color: $text-color;
    i {
      font-size: 16px;
      cursor: pointer;
    }
  }
}
//news

